import React, { Component } from 'react'
import { Card, Form, Button } from 'react-bootstrap'
import '../vendor/styles/pages/authentication.scss'

class Page2 extends Component {
    constructor(props) {
        super(props)
        props.setTitle('Login v3 - Pages')

        this.state = {
            credentials: {
                email: '',
                password: '',
                rememberMe: false
            }
        }
    }

    onValueChange(field, e) {
        this.setState({
            credentials: {
                ...this.state.credentials,
                [field]: field === 'rememberMe' ? e.target.checked : e.target.value
            }
        })
    }

    prevent(e) {
        e.preventDefault()
    }

    render() {
        return (
            <div className="authentication-wrapper authentication-2 ui-bg-cover ui-bg-overlay-container px-4" style={{ backgroundImage: `url('${process.env.PUBLIC_URL}/img/bg/ws/tennis.jpg')` }}>
                <div className="authentication-inner py-5">

                    <Card>
                        <div className="p-4 p-sm-5">

                            <div className="display-1 lnr lnr-mustache text-center text-primary mb-4"></div>

                            <p className="text-center text-big mb-4">Thank you for reaching out to us!<br/>We will get back to you shortly.</p>

                        </div>
                        <Card.Footer className="py-3 px-4 px-sm-5">
                            <div className="text-center text-muted">
                                <a href="#d" onClick={this.prevent}>Back to Dashboard</a>
                            </div>
                        </Card.Footer>
                    </Card>

                </div>
            </div>
        )
    }
}

export default Page2
